.ant-layout {
  background: transparent;
}
.ant-layout .ant-layout-content {
  overflow: auto;
}
.ant-layout .ant-layout-footer {
  display: none;
}
.teacher-manager {
  display: flex;
}
.teacher-manager-card {
  flex: 1 1;
  min-width: 310px;
  margin: 10px;
  max-width: 500px;
  height: 220px;
  padding: 0 10px;
  box-shadow: 4px 4px 5px #888888;
}
.teacher-manager-card .card-top {
  height: 180px;
  width: 100%;
  display: flex;
  align-items: center;
}
.teacher-manager-card .card-top-text {
  flex: 1 1;
  text-align: center;
}
.teacher-manager-card .card-top-text .text-title {
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 24px;
  margin-bottom: 15px;
}
.teacher-manager-card .card-top-text .text-p {
  font-size: 16px;
}
.teacher-manager-card .card-bottom {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.teacher-manager-card .card-bottom a {
  flex: 1 1;
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  margin-bottom: 30px;
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  color: #000;
  text-align: center;
}
.paper {
  padding: 0 30px;
}
.paper .paper-card,
.paper .paper-Input {
  display: flex;
}
.paper .paper-card .paper-span,
.paper .paper-Input .paper-span {
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 16px;
  min-width: 80px;
  text-align: right;
  padding-right: 16px;
}
.paper .paper-card {
  width: 230px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}
.paper .paper-Input {
  margin-top: 10px;
}
.paper .paper-Input .input {
  width: calc(100% - 80px);
  height: 200px;
}
.paper .foot-span {
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 16px;
  min-width: 80px;
  text-align: right;
  padding-right: 16px;
}
.paper .TextArea {
  width: calc(100% - 80px);
  height: 200px;
}
.paper .foot-btn {
  display: block;
  margin: 20px auto auto auto;
}
.showPaper {
  display: flex;
  width: 100%;
  height: 100%;
}
.showPaper-left {
  width: 200px;
  height: 100%;
}
.showPaper-right {
  width: calc(100% - 230px);
  min-width: 900px;
  height: 100%;
}
.showPaper-right .showPaper-tab {
  border-top: 1px solid #888888;
  border-left: 1px solid #888888;
  border-right: 1px solid #888888;
}
.showPaper-right .showPaper-tab .tab-main {
  min-height: 60px;
  display: flex;
  border-bottom: 1px solid #888888;
}
.showPaper-right .showPaper-tab .tab-main-text {
  display: inline-block;
  text-align: center;
  width: 120px;
  min-width: 120px;
  line-height: 80px;
  border-right: 1px solid #888888;
}
.showPaper-right .showPaper-tab .tab-main-span {
  display: inline-block;
  line-height: 60px;
  width: 120px;
  color: #169bd5;
  text-align: center;
}
.showPaper-right .showPaper-tab .tab-main .selected {
  color: #ff9200;
}
.showPaper-right .showPaper-content {
  margin: 30px auto 10px auto;
  width: 80%;
  height: calc(100% - 330px);
}
.showPaper-right .showPaper-content .content-main {
  position: relative;
  width: 100%;
  min-height: 200px;
  border: 1px solid #888888;
  border-radius: 5px;
  padding-bottom: 40px;
  margin-bottom: 10px;
}
.showPaper-right .showPaper-content .content-main .foot {
  position: absolute;
  bottom: 10px;
  left: 0;
  width: 100%;
  height: 30px;
  text-align: center;
}
.showPaper-right .showPaper-content .content-main .foot .showButton {
  width: 117px;
  height: 30px;
  border-radius: 10px;
  background-color: #0099ff;
}
.showPaper-right .showPaper-content .content-main .foot .addButton {
  float: right;
  margin: 5px 20px 0 0;
  color: #169bd5;
}
.createPlan {
  display: flex;
  position: relative;
  width: 100%;
  height: 100%;
}
.createPlan-teacher {
  position: absolute;
  top: -53px;
  right: 230px;
  width: 128px;
  line-height: 45px;
  text-align: center;
  background-color: #169bd5;
  border: none;
  border-radius: 15px;
}
.createPlan-left {
  width: 200px;
  height: 100%;
}
.createPlan-right {
  width: calc(100% - 230px);
  min-width: 900px;
  height: 100%;
}
.createPlan-right .plan-content {
  height: 120px;
  border-top: 1px solid #888888;
  border-left: 1px solid #888888;
  border-right: 1px solid #888888;
}
.createPlan-right .plan-content .tab-main {
  height: 60px;
  display: flex;
  border-bottom: 1px solid #888888;
}
.createPlan-right .plan-content .tab-main-text {
  display: inline-block;
  text-align: center;
  width: 120px;
  line-height: 80px;
  border-right: 1px solid #888888;
}
.createPlan-right .plan-content .tab-main-span {
  display: inline-block;
  line-height: 60px;
  width: 120px;
  color: #169bd5;
  text-align: center;
}
.createPlan-right .plan-content .tab-main .selected {
  color: #ff9200;
}
.createPlan-right .plan-main {
  width: 100%;
  height: calc(100% - 130px);
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.addPlan {
  width: 80%;
  height: 100%;
  margin: auto;
  border: 1px solid #888;
  overflow: auto;
  padding-bottom: 10px;
}
.addPlan-card {
  position: relative;
  margin-top: 20px;
  padding: 30px 30px 10px 100px;
}
.addPlan-card .card-title {
  position: absolute;
  left: 30px;
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  color: #555555;
}
.addPlan-card .card-teacher {
  display: flex;
}
.addPlan-card .card-teacher .teacher-detail,
.addPlan-card .card-teacher .course-detail,
.addPlan-card .card-teacher .time-detail {
  flex: 1 1;
  margin-left: 10px;
  border: 1px solid #888;
}
.addPlan-card .card-teacher .teacher-detail .left,
.addPlan-card .card-teacher .course-detail .left,
.addPlan-card .card-teacher .time-detail .left {
  display: inline-block;
  width: 30%;
  min-width: 80px;
  border-right: 1px solid #888;
  background-color: #22c4f7;
  text-align: center;
}
.addPlan-card .card-source {
  margin-top: 10px;
  display: flex;
  border-top: 1px solid #888;
  line-height: 30px;
}
.addPlan-card .card-source .plan-topic {
  flex: 1 1;
  padding-left: 10px;
}
.addPlan-card .plan-source {
  height: 80px;
  border-top: 1px solid #888;
}
.addPlan .addPlan-content {
  padding-left: 90px;
  padding-right: 30px;
  color: #333333;
}
.addPlan .addPlan-content .content-title {
  display: inline-block;
  line-height: 110px;
  width: 100%;
  text-align: center;
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  font-weight: 700;
  margin-top: 20px;
  background-color: rgba(242, 242, 242, 0.82745098);
  border: 1px solid #888;
}
.addPlan .addPlan-content .content-left {
  width: calc(100% - 120px);
}
.addPlan .addPlan-content .content-left .content-Input {
  margin-top: 20px;
  border: 1px solid #888;
  padding: 10px;
  position: relative;
}
.addPlan .addPlan-content .content-left .content-Input .TextArea {
  min-height: 300px;
  border-color: transparent;
}
.addPlan .addPlan-content .content-left .content-Input .TextArea:hover {
  border-color: #fff !important;
}
.addPlan .addPlan-content .content-left .content-Input .TextArea:focus {
  border-color: #fff !important;
}
.addPlan .addPlan-content .content-left .content-Input .btn-span {
  color: #169bd5;
}
.addPlan .addPlan-content .content-left .content-btn {
  margin-top: 30px;
  display: inline-block;
  text-align: center;
  width: 236px;
  line-height: 71px;
  border-radius: 5px;
  border: 1px dashed #888;
}
.addPlan .addPlan-content .content-right {
  margin-top: 20px;
  width: 100px;
  margin-left: 20px;
  border: 1px solid #888;
}
.addPlan .addPlan-content .content-right .title {
  display: inline-block;
  border-bottom: 1px solid #888;
  width: 100%;
  text-align: center;
  line-height: 30px;
}
.addPlan .addPlan-content .content-right .right-Input {
  width: 100%;
  height: 100%;
}
.addPlan .addPlan-content .content-right .right-Input .TextArea {
  height: 100% - 31px;
  border-color: transparent;
  padding: 0;
}
.addPlan .addPlan-content .content-div {
  width: 100%;
  height: 200px;
}
.addPlan .addPlan-content .content-modal {
  width: 100%;
  height: 200px;
  border: 1px solid #888;
  margin-top: 10px;
  padding: 10px;
}
.addPlan .addPlan-content .content-modal .parentName {
  float: right;
}
.addPlan .addPlan-content .content-modal .parentName .text {
  display: inline-block;
  width: 200px;
  border: none;
}
.addPlan .foot {
  width: 171px;
  height: 40px;
  margin: 5px auto auto auto;
}
